html,body{
margin:0px;
}
.titlehead{
    color:red;
}
.nav-head{
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-around;
    align-items: center;
    position:relative;
    top:0px;
    background-color: rgb(153, 204, 255,0.5);
    width: 100%;
}
.nav-head>section{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-bar{
    display: flex;
    list-style: none;
}
.nav-link{
    padding: 0 10px;
    text-decoration: none;
}

@media screen and (max-width:768px){
    .nav-head{
        display: grid;
        grid-template-columns: auto;
        row-gap: 10px;
        justify-items: center;
    }
    .nav-head >section > img{
        width: 50px;
        height: 50px;
    }
}
